.ngo-dashboard {
    font-family: Arial, sans-serif;
  }
  
  .ngo-dashboard header {
    background-color: #333;
    color: #fff;
    padding: 20px;
  }
  
  .ngo-dashboard h1 {
    margin: 0;
  }
  
  .ngo-dashboard nav ul {
    list-style-type: none;
    padding: 0;
  }
  
  .ngo-dashboard nav ul li {
    display: inline;
    margin-right: 10px;
  }
  
  .ngo-dashboard nav ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  .ngo-dashboard main {
    padding: 20px;
  }