.philanthropist-dashboard {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .philanthropist-dashboard header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .philanthropist-dashboard h1 {
    font-size: 24px;
  }
  
  .philanthropist-dashboard nav ul {
    list-style-type: none;
    display: flex;
  }
  
  .philanthropist-dashboard nav ul li {
    margin-right: 10px;
  }
  
  .philanthropist-dashboard button {
    padding: 8px 16px;
    font-size: 14px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .philanthropist-dashboard button:hover {
    background-color: #0056b3;
  }
  
  .philanthropist-dashboard section {
    margin-bottom: 20px;
  }
  
  .philanthropist-dashboard h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .philanthropist-dashboard .profile-info label {
    display: block;
    margin-bottom: 10px;
  }
  
  .philanthropist-dashboard .profile-info input {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .philanthropist-dashboard .profile-info button {
    margin-top: 10px;
  }
  
  .philanthropist-dashboard table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .philanthropist-dashboard th,
  .philanthropist-dashboard td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .philanthropist-dashboard th {
    background-color: #f2f2f2;
  }
  
  .philanthropist-dashboard .available-ngos ul {
    list-style-type: none;
    padding: 0;
  }
  
  .philanthropist-dashboard .available-ngos li {
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .philanthropist-dashboard .available-ngos h3 {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  .philanthropist-dashboard .available-ngos p {
    margin-bottom: 10px;
  }
  
  .philanthropist-dashboard .available-ngos button {
    background-color: #28a745;
  }
  
  .philanthropist-dashboard .available-ngos button:hover {
    background-color: #218838;
  }